    res.forEach((item, index) => {
  const delay = (index * 5000) / res.length; // 计算每个飞线动画的延迟时间

  setTimeout(() => {
    chartRef.value.addData("flyLine", [
      {
        from: {
          lon: item.From.longitude,
          lat: item.From.latitude,
          style: {
            // color: 'rgba(0,0,0,0)',
          },
        },
        to: {
          lon: item.To.longitude,
          lat: item.To.latitude,
          style: {
            // color: 'rgba(0,0,0,0)',
          },
        },
        style: {
          flyLineStyle: {
            color: "#cd79ff",
            duration: 2000, // 每个飞线动画持续 1 秒
            delay: 0, // 延迟时间由外部 setTimeout 控制
            repeat: 0, // 循环次数为无限
            onComplete: (params) => {
              chartRef.value.remove('flyLine', [`${params.from.lon}${params.from.lat}-${params.to.lon}${params.to.lat}`]);
            },
          },
        },
      },
    ]);
  }, delay);
});
    
  })
  .catch(error => {
    // Handle the error
    console.error(error);
  });
}


