      nodeInfo.value.software = params?.userData.software
      nodeInfo.value.mechine = params?.userData.mechine
      nodeInfo.value.maxqps = params?.userData.maxqps
      if(event.clientX < 900){
        centerDialog.style.left = `${event.clientX}px`;
